<template lang="pug">
  div(class="modal-card-diamond-root")
    i(:style="{'background-image':`url(${require('@/assets/icon/diamond.png')})`}")
    div
      p(v-html="msg")
      div
        button(@click="$emit('cancel')") 取消
        button(@click="$emit('confirm')") 確認
</template>

<script>
export default {
  props: {
    msg: String
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";

.modal-card-diamond-root {
  box-sizing: border-box;
  display: grid;
  grid-template-columns: 85px 170px;
  grid-template-rows: 64px;
  width: 283px;
  height: 92px;
  padding: 13px;
  border: 1px solid map-get($colors, white);
  box-shadow: map-get($shadow, secondary-4);
  background-color: rgba($color: map-get($colors, black), $alpha: 0.7);
  border-radius: 7.5px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -40%);

  > i {
    background-size: cover;
    width: 100px;
    height: 90px;
    position: relative;
    top: -35px;
    left: -15px;
  }
  > div {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    > p {
      width: 150px;
      height: 34px;
      font-size: 14px;
      line-height: 17px;
      font-weight: 900;
      text-align: left;
    }
    > div {
      display: flex;
      justify-content: flex-end;
      > button {
        font-size: 14px;
        font-weight: 900;
      }
      > button:nth-child(2) {
        color: #ff417c;
        margin-left: 11px;
      }
    }
  }
}
</style>
