<template lang="pug">
  div(class="buildgin-feature-root")
    architecture-technology-center(
      v-if="building.feature.component === 'architectureTechnology'"
      :planet="planet"
      @nextScenes="nextScenes")
    stone-portal(
      v-if="building.feature.component === 'stonePortal'"
      :user="user"
      :planet="planet"
      :building="building")
    hades-temple(
      v-if="building.feature.component === 'hadesTemple'"
      :user="user")
    city-hall(
      v-if="building.feature.component === 'cityHall'"
      :user="user"
      :planet="planet"
    )
</template>

<script>
import ArchitectureTechnologyCenter from '@/components/game/build/buildingFeature/ArchitectureTechnologyCenter.vue'
import StonePortal from '@/components/game/build/buildingFeature/StonePortal.vue'
import HadesTemple from '@/components/game/build/buildingFeature/HadesTemple.vue'
import CityHall from '@/components/game/build/buildingFeature/CityHall.vue'
import { mapActions } from 'vuex'

export default {
  components: {
    ArchitectureTechnologyCenter,
    StonePortal,
    HadesTemple,
    CityHall
  },
  props: {
    user: Object,
    planet: Object,
    building: Object
  },
  data () {
    return {
    }
  },
  created () {
    if (this.building.feature.component === 'stonePortal') {
      this.getPlanetList()
    }
  },
  methods: {
    ...mapActions('user', ['getPlanetList']),
    nextScenes (action) {
      this.$emit('nextScenes', action)
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";

.buildgin-feature-root {
}
</style>
